<template>
  <div class="profile-card">
    <img class="profile-picture" src="../assets/profile-picture.jpg">
    <div>{{ name }}</div>
    <small>
      {{ title }} <br>
      📍{{ location }} <br>
      📞<a :href="`tel:+1${phone}`">{{ decoratedPhone }}</a> <br>
      ✉️<a :href="`mailto:${email}`">{{ email }}</a> <br>
    </small>
  </div>
</template>

<script>
export default {
  name: 'ProfileCard',
  props: {
    name: String,
    title: String,
    phone: Number,
    email: String,
    location: String,
  },
  computed: {
    decoratedPhone() {
      const cleaned = (`${this.phone}`).replace(/\D/g, '');
      const match = cleaned.match(/^(\d{3})(\d{3})(\d{4})$/);
      if (match) {
        return `+1 (${match[1]}) ${match[2]}-${match[3]}`;
      }
      return null;
    },
  },
};
</script>

